* { box-sizing: border-box; margin: 0; padding: 0; }

html, body { position: relative; height: 100%; }

.wrapper { min-height: 100vh; display: flex; flex-direction: column; align-items: stretch; justify-content: flex-start; }

footer { margin: auto 0 0; }

.body-overflow { overflow: hidden; overscroll-behavior-y: none; -webkit-overflow-scrolling: auto; }

.container { max-width: 1250px; width: 100%; position: relative; margin: 0 auto; padding: 0 15px; }

.visibility--hidden { position: absolute; clip: rect(0 0 0 0); width: 1px; height: 1px; margin: -1px; }

.sr-only { position: absolute; clip: rect(1px, 1px, 1px, 1px); clip-path: inset(50%); width: 1px; height: 1px; overflow: hidden; padding: 0; border: 0; white-space: nowrap; }

picture, img { max-width: 100%; height: auto; display: block; }

